import React, { memo, useCallback, useMemo, useState } from 'react';

import { __ } from '@wp-plugins/i18n';
import { Button, ButtonProps } from '@wp-plugins/adapters';
import { testBotToken, TestResult, TestResultType } from '@wp-plugins/services';

import { RenderTestResult } from './TestResult';

export type BotTokenTest = {
	bot_token: string;
	buttonComponent: React.ComponentType<ButtonProps>;
	buttonNode: React.ReactNode;
	resultNode: React.ReactNode;
	bot_username?: string;
	testResult: TestResult;
	testResultType: TestResultType;
};

export const useBotTokenTest = (_bot_token_?: string, hasError?: boolean): BotTokenTest => {
	const [testingBotToken, setTestingBotToken] = useState('');

	const [bot_token, setBotToken] = useState(_bot_token_ || '');
	const [bot_username, setBotUsername] = useState('');

	const [testResult, setTestResult] = useState<TestResult>({});
	const [testResultType, setTestResultType] = useState<TestResultType>({});

	const onTestToken = useCallback(
		(bot_token: string) => async (event: React.MouseEvent) => {
			setBotToken(bot_token);
			setBotUsername('');

			await testBotToken(
				{
					bot_token,
					setInProgress: (inProgress) => setTestingBotToken(inProgress && bot_token),
					setResult: (result) => {
						setTestResult((prevState) => ({
							...prevState,
							[bot_token]: result,
						}));
					},
					setResultType: (resultType) => {
						setTestResultType((prevState) => ({
							...prevState,
							[bot_token]: resultType,
						}));
					},
					onComplete: (token, { username }) => {
						setBotToken(token);
						if (username) {
							setBotUsername(username);
						}
					},
				},
				event
			);
		},
		[]
	);

	const onClickTest = useCallback<React.MouseEventHandler>(
		async (event) => {
			await onTestToken(bot_token)(event);
		},
		[bot_token, onTestToken]
	);

	const buttonComponent = memo<ButtonProps>(({ value: bot_token, isDisabled }) => {
		return (
			<Button
				isDisabled={!bot_token || isDisabled || Boolean(testingBotToken)}
				onClick={onTestToken(bot_token as string)}
				borderStartRadius='0'
			>
				{testingBotToken && testingBotToken === bot_token ? __('Please wait') : __('Test Token')}
			</Button>
		);
	});

	return useMemo(() => {
		const buttonNode = (
			<Button
				onClick={onClickTest}
				borderStartRadius='0'
				isDisabled={!bot_token || hasError || Boolean(testingBotToken)}
			>
				{testingBotToken && testingBotToken === bot_token ? __('Please wait') : __('Test Token')}
			</Button>
		);

		const resultNode = <RenderTestResult result={testResult[bot_token]} resultType={testResultType[bot_token]} />;

		return { bot_token, buttonNode, buttonComponent, resultNode, bot_username, testResult, testResultType };
	}, [testResult, testResultType, bot_token, bot_username, buttonComponent, hasError, onClickTest, testingBotToken]);
};
