import { useWatch } from '@wp-plugins/form';
import { Collapse } from '@wp-plugins/adapters';

export const IfActive: React.FC<{ name: string }> = ({ children, name }) => {
	const active = useWatch<boolean>({ name });

	return (
		<Collapse in={active} animateOpacity>
			{children}
		</Collapse>
	);
};
